/* * NombreDeProyecto * ========================================================
   Form Settings | Etc [/etc/css/scss/settings_form.scss]
   ========================================================================== */

@use "var_all_pre" as *;
@use "../custom/var/env_var" as *;

@use "sass:color";
@use "sass:map";
/* // Descripcion ----------------------------------------------------------- */
// Archivo de estilos scss.



/* // Referencias globales de nombres de variables y mixins ----------------- */
///-- Inicio del nombre
// bd                   = borde
// bg                   = fondo / background
// bx                   = box
// ft                   = fuente
// mq, MQ               = media query @media
// tx                   = texto

///-- Fin del nombre
// _before              = se usa con pseudo-elementos ::before
// _after               = se usa con pseudo-elementos ::after
// _act                 = activacion
// _ani                 = animacion
// _hover               = se usa con pseudo-clases :focus y :hover
// _hs                  = hash
// _imp			= importacion
// _mx                  = mixin
// _var                 = variables

///-- Espaciado
// P, M                 = padding, margin
// T, R, B, L           = top, right, bottom, left
// H, V, A, X           = horizontal, vertical, all, custom defined

///-- Tamanos de Pantalla
// T                    = tablet
// B, A, O, L           = before, after, only, landscape

///-- Colores
// X, L, D              = extra, light, dark
// De, S, B             = desaturated, semi, bright

///-- Fuentes
// T, L, M, R, B, BK    = thin, light, medium, regular, bold, black
// X, S, U, C, I        = extra, semi, ultra, condensed, italic



form, fieldset, label, input, select, textarea, button,
.form_label_name, .form_validation_span{
    @include beforeMQ768{
        .mobileForm &{
            clear: both!important;
            float: $none;
            margin-right: $auto;
            margin-left: $auto;
        }
    }
    
    @include MQ768_1024{
        .tabletForm &{
            clear: both!important;
            float: $none;
            margin-right: $auto;
            margin-left: $auto;
        }
    }
}

fieldset, label, input, select, textarea, button,
.form_validation_div, .form_validation_span, .form_status, .form_label_name{
    .mobileForm &{        
        @include beforeMQ350{
            width: $hundred;
            max-width: $form_label_width_max_B350;          
        }
        
        @include afterMQ350{
            width: $form_label_width_A350;
        }
        
        @include afterMQ500{
            width: $form_label_width_A500;
        }
    }
    
    @include MQ768_1024{
        .tabletForm &{
            width: $form_label_width_TO;
        }
    }
    
    @include afterMQ1024{
        form &{
            width: $form_label_width_A1024;
            margin-right: auto;
            margin-left: auto;
        }
    }
    
    @include afterMQ1200{
        form &{
            width: $form_label_width_A1200;
        }
    }
}

fieldset, label, button,
.form_validation_div, .form_validation_span, .form_status, .form_label_name{
    @include beforeMQ350{
        .mobileForm &{
            font-size: $form_ftSize_B350;
        }
    }
    
    @include afterMQ350{
        .mobileForm &{
            font-size: $form_ftSize_A350;
        }
    }
    
    @include MQ768_1024{
        .tabletForm &{
            font-size: $form_ftSize_AT;
        }
    }
    
    @include afterMQ1024{
        form &{
            font-size: $form_ftSize_A1024;
        }
    }
}

input, select, textarea{
    caret-color: $form_input_caret_colour;
    
    .mobileForm &{
        @include beforeMQ768{
            font-size: $form_input_ftSize_BT;
        }
    }
    
    @include MQ768_1024{
        .tabletForm &{
            font-size: $form_input_ftSize_AT;
        }
    }
    
    @include afterMQ1024{
        form &{
            font-size: $form_input_ftSize_A1024;
        }
    }
    
    @include afterMQ1200{
        form &{
            font-size: $form_input_ftSize_A1200;
        }
    }
}

/* // Form Button Submit // REF [21] ---------------------------------------- */
input[type="reset"],
input[type="submit"],
input[type="button"],
button{
    .mobileForm &{
        @include beforeMQ500{
            width: $button_form_submit_width_B500!important;
        }

        @include MQ500_768{
            width: $button_form_submit_width_500O;   
        }

        @include beforeMQ768{
            height: $button_form_submit_height_BT;
            line-height: $button_form_submit_lineHeight_BT;
        }
    }
    
    .tabletForm &{
        @include MQ768_1024{
            width: $button_form_submit_width_TO;
            height: $button_form_submit_height_TO;
            line-height: $button_form_submit_lineHeight_TO;
        }  
    }

    @include afterMQ1024{
        form &{
            width: $button_form_submit_width_A1024!important;
            height: $button_form_submit_height_A1024;
            line-height: $button_form_submit_lineHeight_A1024;
        }
    }

    @include afterMQ1200{
        form &{
            width: $button_form_submit_width_A1200!important;
            height: $button_form_submit_height_A1200;
            line-height: $button_form_submit_lineHeight_A1200;
        }
    }
    
    & + &{
        margin-top: $helpers_spacing_PM_large;
    }
}

/* // Form Input ------------------------------------------------------------ */
form{
    input[type="text"],
    input[type="email"],
    input[type="search"],
    input[type="tel"],
    input[type="url"],
    input[type="number"],
    input[type="time"],
    input[type="date"],
    input[type="month"],
    input[type="week"],
    select,
    textarea,
    input[type="text"]:-webkit-autofill,
    input[type="email"]:-webkit-autofill,
    input[type="password"]:-webkit-autofill,
    input[type="search"]:-webkit-autofill,
    input[type="tel"]:-webkit-autofill,
    input[type="url"]:-webkit-autofill,
    input[type="number"]:-webkit-autofill,
    input[type="time"]:-webkit-autofill,
    input[type="date"]:-webkit-autofill,
    input[type="month"]:-webkit-autofill,
    input[type="week"]:-webkit-autofill,

    input[type="text"]:-webkit-autofill:hover,
    input[type="email"]:-webkit-autofill:hover,
    input[type="password"]:-webkit-autofill:hover,
    input[type="search"]:-webkit-autofill:hover,
    input[type="tel"]:-webkit-autofill:hover,
    input[type="url"]:-webkit-autofill:hover,
    input[type="number"]:-webkit-autofill:hover,
    input[type="time"]:-webkit-autofill:hover,
    input[type="date"]:-webkit-autofill:hover,
    input[type="month"]:-webkit-autofill:hover,
    input[type="week"]:-webkit-autofill:hover,

    input[type="time"]:-webkit-autofill::-moz-selection,
    input[type="date"]:-webkit-autofill::-moz-selection,
    input[type="month"]:-webkit-autofill::-moz-selection,
    input[type="week"]:-webkit-autofill::-moz-selection,

    input[type="time"]:-webkit-autofill::selection,
    input[type="date"]:-webkit-autofill::selection,
    input[type="month"]:-webkit-autofill::selection,
    input[type="week"]:-webkit-autofill::selection,

    input[type="time"]:-webkit-autofill::-moz-placeholder,
    input[type="date"]:-webkit-autofill::-moz-placeholder,
    input[type="month"]:-webkit-autofill::-moz-placeholder,
    input[type="week"]:-webkit-autofill::-moz-placeholder,

    input[type="time"]:-webkit-autofill::input-placeholder,
    input[type="date"]:-webkit-autofill::input-placeholder,
    input[type="month"]:-webkit-autofill::input-placeholder,
    input[type="week"]:-webkit-autofill::input-placeholder,

    input[type="time"]:-webkit-autofill::-webkit-input-placeholder,
    input[type="date"]:-webkit-autofill::-webkit-input-placeholder,
    input[type="month"]:-webkit-autofill::-webkit-input-placeholder,
    input[type="week"]:-webkit-autofill::-webkit-input-placeholder,

    input[type="time"]:-webkit-autofill:-ms-input-placeholder,
    input[type="date"]:-webkit-autofill:-ms-input-placeholder,
    input[type="month"]:-webkit-autofill:-ms-input-placeholder,
    input[type="week"]:-webkit-autofill:-ms-input-placeholder,

    input[type="text"]:-webkit-autofill:focus,
    input[type="email"]:-webkit-autofill:focus,
    input[type="password"]:-webkit-autofill:focus,
    input[type="search"]:-webkit-autofill:focus,
    input[type="tel"]:-webkit-autofill:focus,
    input[type="url"]:-webkit-autofill:focus,
    input[type="number"]:-webkit-autofill:focus,
    input[type="time"]:-webkit-autofill:focus,
    input[type="date"]:-webkit-autofill:focus,
    input[type="month"]:-webkit-autofill:focus,
    input[type="week"]:-webkit-autofill:focus,

    input[type="text"]:-webkit-autofill:focus-visible,
    input[type="email"]:-webkit-autofill:focus-visible,
    input[type="password"]:-webkit-autofill:focus-visible,
    input[type="search"]:-webkit-autofill:focus-visible,
    input[type="tel"]:-webkit-autofill:focus-visible,
    input[type="url"]:-webkit-autofill:focus-visible,
    input[type="number"]:-webkit-autofill:focus-visible,
    input[type="time"]:-webkit-autofill:focus-visible,
    input[type="date"]:-webkit-autofill:focus-visible,
    input[type="month"]:-webkit-autofill:focus-visible,
    input[type="week"]:-webkit-autofill:focus-visible,

    textarea:-webkit-autofill,
    textarea:-webkit-autofill:hover,
    textarea:-webkit-autofill:focus,
    textarea:-webkit-autofill:focus-visible,

    select:-webkit-autofill,
    select:-webkit-autofill:hover,
    select:-webkit-autofill:focus,
    select:-webkit-autofill:focus-visible{
        -webkit-text-fill-color: $form_input_ftColour_autofill;
        -webkit-box-shadow: 0 0 0 0 #fff inset;
    }
    
    &, fieldset, legend, button, input, select, textarea{
        margin: 0;
        padding: 0;
    }
    
    &, label{
        position: relative;
    }
    
    fieldset{
        &, legend{
            border: $none;
        } 
        
        legend{
            white-space: normal;/* Corrects text not wrapping in Firefox 3. */
            *margin-left: -7px;/* Corrects alignment displayed oddly in IE 6/7. */
        }
    }
    
    label{
        @include transition(1s);
    }
    
    label,
    .form_label_name{
        color: $form_label_ftColour;
        font-weight: 400;

        &:focus,
        &:focus-visible,
        &:hover{
            color: $form_label_ftColour_hover;
        }
    }
        
    button, input, select, textarea{
        vertical-align: baseline;/* Improves appearance and consistency in all browsers. */
        *vertical-align: middle;/* Improves appearance and consistency in all browsers. */
    }
    
    @media screen and (-webkit-min-device-pixel-ratio: 0) {/* chrome */
        button, input, select, option, textarea{
            &:focus, &:active{
                outline: 1px dotted #999;            
            }
        }
    }
    
    input, select, textarea{
        transition: all 1s ease, background-position 0s ease;
        background-color: $form_input_bgColour;
        color: $form_input_ftColour;
        font-family: $form_input_ftFamily; 

        @if $google_fonts_act == "on"{
            font-weight: $form_input_ftWeight;
        }
        
        &:focus{
            box-shadow: $form_input_shadowInset;
        }

        &:focus, &:focus-visible, &:hover{
            background-color: $form_input_bgColour_hover;
            color: $form_input_ftColour_hover;
        }  
        
        &, &::before, &::after{
            box-sizing: border-box;
        }
    }
    
    input{
        &[type="search"]{
            -webkit-appearance: textfield;
        }

        &[type="checkbox"],
        &[type="radio"]{
            width: $auto;
        }

        &[type="submit"]{
            -webkit-appearance: none;
        }
    }
    
    input::focus-inner{
        margin: 0;
        border: $none;
        padding: 0;
    }
    
    textarea{
        padding: $helpers_spacing_PM_medium;
        vertical-align: top;
        resize: vertical;
    }
    
    .mobileForm &{
        @include beforeMQ350{
            width: 90%;
            max-width: $form_width_max_B350;            
        }
        
        @include afterMQ350{
            width: $form_width_A350;
        }
        
        @include afterMQ500{
            width: $form_width_A500;
        }
        
        @include MQ600_768{
            width: $form_width_600Only;
        }
    } 
    
    .tabletForm &{
        @include afterMQ768{
            width: $form_width_AT;
        }
        
        @include MQ768_1024{
            width: $form_width_TO;
        }
    
        @include afterMQ1024{
            width: $form_width_A1024!important;
        }
    }
    
    @include afterMQ1024{
        width: $form_width_A1024!important;
    }
    
    @include afterMQ1200{
        width: $form_width_A1200;
    }
    
    @if $form_label_inside_act == "off"{
        label, legend{
            display: block\0/!important; /* IE8 + IE9 */
            padding-left: 7px;
            margin-bottom: $zero;
            font-family: $form_label_ftFamily;

            @if $google_fonts_act == "on"{
                font-weight: $form_label_ftWeight;
            }

            @include beforeMQ350{
                .mobileForm &{
                    @include equalHeightAndLineHeight($form_label_height_B350);
                }
            }

            @include afterMQ350{
                .mobileForm &{
                    @include equalHeightAndLineHeight($form_label_height_A350);
                }
            }

            @include MQ768_1024{
                .tabletForm &{
                    @include equalHeightAndLineHeight($form_label_height_TO);          
                }
            }

            @include afterMQ1024{
                @include equalHeightAndLineHeight($form_label_height_A1024);
            }

            @include afterMQ1200{
                @include equalHeightAndLineHeight($form_label_height_A1200);
            }
        }

        @media all and (-ms-high-contrast:none){
            label {/* IE10 */
                display: $block;
            }

            *::-ms-backdrop, label {/* IE11 */
                display: $block;
            }
        }

        input, select{
            @include beforeMQ768{
                .mobileForm &{
                    @include equalHeightAndLineHeight($form_input_height_BT);
                }
            }

            @include MQ768_1024{
                .tabletForm &{
                    @include equalHeightAndLineHeight($form_input_height_TO);          
                }
            }

            @include afterMQ1024{
                @include equalHeightAndLineHeight($form_input_height_A1024);
            }

            @include afterMQ1200{
                @include equalHeightAndLineHeight($form_input_height_A1200);
            }
        }

        input, select, textarea{
            @include beforeMQ768{
                .mobileForm &{
                    margin-bottom: $helpers_spacing_PM_large;
                }
            }

            @include MQ768_1024{
                .tabletForm &{
                    margin-bottom: $helpers_spacing_PM_large;       
                }
            }

            @include afterMQ1024{
                margin-bottom: $helpers_spacing_PM_medium;
            }
        }
    }
    
    @if $form_label_inside_act == "on"{
        fieldset, label{
            @include clearfix;
        }
    
        input, select{
            @include beforeMQ768{
                .mobileForm &{
                    @include equalHeightAndLineHeight($form_input_height_BT);
                }        
            }

            @include MQ768_1024{
                .tabletForm &{
                    @include equalHeightAndLineHeight($form_input_height_TO);
                }
            }

            @include afterMQ1024{
                @include equalHeightAndLineHeight($form_input_height_A1024);
            }

            @include afterMQ1200{
                @include equalHeightAndLineHeight($form_input_height_A1200);
            }
        }
        
        .form_label_name,
        .data_password_label .form_label_msg_sugg{ /* // REF [52] Form sugg floated */
            @include beforeMQ350{
                .mobileForm &{
                    @include equalHeightAndLineHeight($form_label_height_B350);
                }
            }

            @include afterMQ350{
                .mobileForm &{
                    @include equalHeightAndLineHeight($form_label_height_A350);
                }
            }

            @include MQ768_1024{
                .tabletForm &{
                    @include equalHeightAndLineHeight($form_label_height_TO);
                }
            }

            @include afterMQ1024{
                @include equalHeightAndLineHeight($form_label_height_A1024);
            }

            @include afterMQ1200{
                @include equalHeightAndLineHeight($form_label_height_A1200);
            }
        }
    }
}